/* FOOTER */

.footer {
    background-color: var(--dark-bg);
    padding: 60px 0 50px;
    color: var(--text-color);
}

.footer__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 27px;
}

.social {
    display: flex;
    column-gap: 30px;
    align-items: center;
}