@import "./reset.css";

@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap');

/* font-family: 'DM Sans', sans-serif;
font-family: 'Poppins', sans-serif; */

:root {
    --black: #171718;
    --black-border: #26292d;
    --white: #FFFAFA;
    --peach: #FFDAB9;

    --text-color: var(--black);
    --dark-bg: var(--black);
    --dark-border: var(--black-border);
    --header-text: var(--white);
    --accent: var(--peach);
}

html, body {
    font-family: 'DM Sans', sans-serif;
    letter-spacing: -0.5px;
    color: var(--text-color);
}

#root {
    min-height: 100vh;
}

.App {
    min-height: 100vh;
}

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}

.title-2 {
    font-size: 40px;
    line-height: 1.3;
    margin-bottom: 20px;
    font-weight: 700;
}

/* NAV */

/* HEADER */

/* MAIN */

.section {
    padding: 70px 0;
}

.title-1 {
    margin-bottom: 60px;
    margin-top: 60px;
    font-size: 60px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--accent);
    text-align: center;
}

/* MAIN PROJECTS */

.projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 30px;
    row-gap: 30px;
}

.project {
    max-width: 370px;
    background-color: var(--white);
    box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}

.project img {
    border-radius: 10px;
    min-height: 200px;
    min-width: 200px;
}

.project__title {
    padding: 15px 20px 25px;
    font-size: 24px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--black);
}

/* PROJECT DETAILS */

.project-details {
    margin: 0 auto ;
    max-width: 865px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-details__cover {
    max-width: 100%;
    margin-bottom: 40px;
    box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.65);
    border-radius: 10px;
}

.project-details__description {
    font-weight: 700;
    font-size: 24px;
    line-height: 1.3;
    margin-bottom: 30px;
}

/* CONTENT-LIST */

.content-list {
    margin: 0 auto;
    max-width: 570px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    row-gap: 40px;
}

.content-list__item {
    font-size: 18px;
    line-height: 1.5;
}

.content-list__item p + p {
    margin-top: 0.5em;
}

.content-list_icon {
    margin: 50px 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 50px;
}

/* FOOTER */

/* MOBILE VERSION */

@media (max-width: 620px) {

    .header {
        min-height: unset;
    }

    .header__title {
        font-size: 30px;
    }

    .header__title strong {
        font-size: 40px;
    }

    .section {
        padding: 40px 0;
    }

    .title-1 {
        margin-bottom: 30px;
        font-size: 40px;
    }

    .title-2 {
        margin-bottom: 10px;
        font-size: 30px;
    }

    .project__title {
        font-size: 22px;
    }

    .project-details__description {
        margin-bottom: 20px;
        font-size: 22px;
    }

    .footer {
        padding: 40px 0 30px;
    }

    .social__item {
        width: 28px;
    }

    .footer__wrapper {
        row-gap: 20px;
    }

    .social {
        column-gap: 20px;
    }

    .content-list {
        row-gap: 20px;
    }

    .content-list__item {
        font-size: 16px;
    }

}